import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/home.vue";
import Works from "@/views/works.vue";
import Profile from "@/views/profile.vue";
import Contact from "@/views/contact.vue";
import GoboPH from "@/works/gobo_philippines.vue";
import Windoway from "@/works/windoway_website.vue";
import Winshade from "@/works/winshade_website.vue";
import Winfurniture from "@/works/winfurniture_website.vue";
import ISDMS from "@/works/isd_monitoring_system.vue";
import ISPRT from "@/works/is_performance_reporting_system.vue";
import AIKPM from "@/works/aikpm_lugang_cafe.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/works",
    name: "Works",
    component: Works
  },
  {
    path: "/profile",
    name: "Profile",
    component: Profile
  },
  {
    path: "/contact",
    name: "Contact",
    component: Contact
  },
  {
    path: "/Gobo_Philippines",
    name: "Gobo Philippines",
    component: GoboPH
  },
  {
    path: "/windoway-website",
    name: "Windoway Website",
    component: Windoway
  },
  {
    path: "/winshade-website",
    name: "Winshade Website",
    component: Winshade
  },
  {
    path: "/winfurniture-website",
    name: "Winfurniture Website",
    component: Winfurniture
  },
  {
    path: "/isd-monitoring-system",
    name: "DENR Information Systems Division Monitoring System",
    component: ISDMS
  },
  {
    path: "/is-performance-reporting-system",
    name: "DENR Information System Performance Reporting System",
    component: ISPRT
  },
  {
    path: "/admin-inventory-kitchen-process-monitoring",
    name: "Adming Inventory Kitchen Process Monitoring for Lugang Cafe",
    component: AIKPM
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
