<template>
    <div id="works_detail" class="common_detail works_block" style="background-color: #1c1c1c;">
        <div class="fv">
            <div class="pic forpc js-img_check" data-color="black"
                :style="`background-image:url('${details.img_pc}');`">
            </div>
            <div class="pic forsp js-img_check" data-color="black"
                :style="`background-image:url('${details.img_sp}');`">
            </div>
        </div>

        <div class="head f_hel js-sc">
            <p class="date js-appear_h tra50">{{details.year}}</p>
            <h2 class="title js-appear_h tra50 delay02">{{details.title}}</h2>
            <h3 class="sub js-appear_h tra50 delay04">
            </h3>
        </div>

        <div class="body">
            <p class="desc js-sc js-appear tra50 delay04">
                {{details.description}}
            </p>
            <ul class="share js-sc js-appear tra50 clearfix on">
                
            </ul>

            <div class="slide_area js-sc js-appear tra50">
                <ul class="slide js-slide">
                    <li v-for="(img,key) in img" :key="key">
                        <div class="pic js-img_check" data-color="black" :style="`background-image:url('${img.img}');`">
                        </div>
                    </li>
                </ul>
                <div class="num js-num f_hel">
                    <p class="now">01</p>
                    <p class="total"></p>
                </div>
                <div class="btn_prev"></div>
                <div class="btn_next"></div>
            </div>
        </div>

    </div>
</template>

<script>
    import IMG from "@/data/aikpm_img.json";
    import DETAILS from "@/data/aikpm.json";
    export default {
        data() {
            return {
                details: DETAILS,
                img: IMG
            };
        }

    }
</script>